<template>
  <div class="pa-6">
    <v-data-table
      :loading="loading"
      :headers="headers"
      :items="transactions.data"
      :items-per-page="5"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-container
          class="grey darken-4 elevation-1 pt-6 px-7"
          style="max-width: 100%!important;"
        >
          <v-row>
            <v-col cols="12" md="12" sm="12" class="primary--text headline">
              <v-btn
                @click="reloadTransactions"
                x-small fab color="primary" class="mr-3 elevation-0"
              >
                <v-icon class="black--text" small>fas fa-redo-alt</v-icon>
              </v-btn>
              <small>Transactions</small>
            </v-col>
          </v-row>
        </v-container>
      </template>
      <template v-slot:item.status="{ item }">
        <div class="justify-center layout">
          <v-chip
            small dark
            :class="item.confirmed ? 'success' : 'primary black--text'"
          >
            {{ item.confirmed ? 'Approved' : 'Pending' }}
          </v-chip>
        </div>
      </template>
      <template v-slot:item.actions="{ item }">
        <div class="justify-center layout">
          <v-btn
            @click="selectedTransactions = item, showEncashmentDialog = true"
            dark fab x-small
            class="elevation-0 ml-2 blue"
          >
            <v-icon dark small>fas fa-search</v-icon>
          </v-btn>
        </div>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data: () => ({
    loading: false,
    transactions: {},
    selectedTransactions: {},
    search: null,
    status: null,
    pagination: { page: 1, itemsPerPage: 10 },
    headers: [
      { text: 'Account', align: 'start', sortable: false, value: 'account.name' },
      { text: 'Amount', align: 'start', value: 'amount' },
      { text: 'Date requested', align: 'start', sortable: true, value: 'created_at' },
      { text: 'Status', align: 'center', sortable: false, value: 'status', width: '120px' },
      { text: 'Actions', align: 'center', sortable: false, value: 'actions', width: '120px' }
    ]
  }),
  mounted () {
    this.getTransactions()
  },
  computed: {
    ...mapGetters({
      selectedAccount: 'selectedAccount',
    })
  },
  watch: {
    status () {
      this.reloadTransactions()
    },
    pagination: {
      handler () {
        if (! this.disablePagination) {
          this.getEncashments()
        }
      },
      deep: true
    },
    selectedAccount () {
      this.reloadTransactions()
    }
  },
  methods: {
    async getTransactions (){
      try {
      this.loading = true
      const p = this.pagination
      const payload = {
        page: p.page, limit: p.itemsPerPage,
        account_id: this.selectedAccount ? this.selectedAccount.id : null,
        confirmed: this.status, user_id: this.userId
      }
      if (this.pagination.sortBy && this.pagination.sortBy.length > 0) {
        this.$set(payload, 'order_by', this.pagination.sortBy[0])
        this.$set(payload, 'order_desc', this.pagination.sortDesc[0] ? 1 : 0)
      }
      const transactions = await this.$axios.get('/user/transactions', { params: payload })
      this.transactions = transactions.data
      } catch (e) {
        this.showSnackbar('error', e.response ? e.response.data.message : e.message)
      } finally {
        this.loading = false
      }
    },
    async reloadTransactions () {
      this.disablePagination = true
      this.pagination.page = 1
      await this.getTransactions()
      this.disablePagination = false
    }
  }
}
</script>